import React from 'react';
import Layout from './../Layout/Layout';
import ArticleApi from './../../api/ArticleApi';
import TagListInput from './TagListInput';
import renderErrorMessages from '../Common/RenderErrorMessage';

export default class EditorRoute extends React.Component {

    constructor(props, context) {
        super(props, context);

        this.state = {
            article: null,
            errors: null,
        };

        if (props.match.params.slug) {
            ArticleApi.getArticle(props.match.params.slug).then((article) => this.setState({ article }));
        } else {
            this.state.article = {};
        }
    }

    doSubmit = (e) => {
        e.preventDefault();
        const { history } = this.props;
        const { article } = this.state;

        const { slug } = this.props.match.params;

        this.setState({ submitting: true });
        (slug ? ArticleApi.updateArticle(article) : ArticleApi.createArticle(article)).then(({ errors, article }) => {
            if (errors) {
                this.setState({ submitting: false, errors });
            }
            else if (article) {
                history.push(`/article/${article.slug}`)
            }
            else {
                window.location.reload();
            }
        });
    }

    render() {
        const { submitting, errors, article } = this.state;

        const bind = (path) => ({
            value: article && article[path],
            onChange: (newValue) => this.setState({ article: { ...article, [path]: newValue } }),
        });

        const bindString = (path) => ({
            value: (article == null || article[path] == null) ? "" : article[path],
            onChange: (e) => this.setState({ article: { ...article, [path]: e.target.value } }),
        });

        return (
            <Layout
                windowTitle="Editor"
            >
                <div className="editor-page">
                    <div className="container page">
                        <div className="row">
                            <div className="col-md-10 col-md-offset-1 col-xs-12">
                                {renderErrorMessages(errors)}
                                <form onSubmit={this.doSubmit}>
                                    <fieldset>
                                        <fieldset className="form-group" disabled={article == null || submitting}>
                                            <input type="text" className="form-control form-control-lg" placeholder="Article Title"
                                                {...bindString("title")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={article == null || submitting}>
                                            <input type="text" className="form-control" placeholder="What's this article about?"
                                                {...bindString("description")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={article == null || submitting}>
                                            <textarea className="form-control" rows="8" placeholder="Write your article (in markdown)"
                                                {...bindString("body")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={article == null || submitting}>
                                            <TagListInput
                                                {...bind("tagList")}
                                            />
                                        </fieldset>
                                        <button className="btn btn-lg pull-xs-right btn-primary">
                                            Publish Article
                                        </button>
                                    </fieldset>
                                </form>
                            </div>

                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}


