import React from 'react';
import Layout from './../Layout/Layout';
import Auth from '../../api/Auth';
import RComponent from '../../common/RComponent';
import userApi from '../../api/UserApi';
import renderErrorMessages from '../Common/RenderErrorMessage';

export default class SettingRoute extends RComponent {

    constructor(props, context) {
        super(props, context);

        this.state = {
            user: Auth.getUser(),
            errors: null,
        };
    }

    doSubmit = (e) => {
        e.preventDefault();
        const { history } = this.props;
        const { user } = this.state;

        this.setState({ submitting: true });

        userApi.updateUser(user).then(({ errors, user }) => {

            if (errors) {
                this.setState({ submitting: false, errors });
            } else {
                Auth.setUser(user);
                history.push(`/@${user.username}`)
            }
        });
    }

    doLogout = (e) => {
        e.preventDefault();
        Auth.setUser(null);
    }

    render() {
        const { submitting, errors, user } = this.state;

        const bind = (path) => ({
            value: user[path] == null ? "" : user[path],
            onChange: (e) => this.setState({ user: { ...user, [path]: e.target.value == "" ? null : e.target.value } }),
        });

        return (
            <Layout
                windowTitle="Settings"
            >
                <div className="settings-page">
                    <div className="container page">
                        <div className="row">
                            <div className="col-md-6 col-md-offset-3 col-xs-12">
                                <h1 className="text-xs-center">Your Settings</h1>
                                {renderErrorMessages(errors)}
                                <form
                                    onSubmit={this.doSubmit}
                                >
                                    <fieldset>
                                        <fieldset className="form-group" disabled={submitting}>
                                            <input className="form-control" type="text" placeholder="URL of profile picture"
                                                {...bind("image")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={submitting}>
                                            <input className="form-control form-control-lg" type="text" placeholder="Your Name"
                                                {...bind("username")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={submitting}>
                                            <textarea className="form-control form-control-lg" rows="8" placeholder="Short bio about you"
                                                {...bind("bio")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={submitting}>
                                            <input className="form-control form-control-lg" type="text" placeholder="Email"
                                                {...bind("email")}
                                            />
                                        </fieldset>
                                        <fieldset className="form-group" disabled={submitting}>
                                            <input className="form-control form-control-lg" type="password" placeholder="New Password"
                                                {...bind("password")}
                                            />
                                        </fieldset>
                                        <button className="btn btn-lg btn-primary pull-xs-right">
                                            Update Settings
                                        </button>
                                    </fieldset>
                                </form>
                                <hr />
                                <button
                                    className="btn btn-outline-danger"
                                    onClick={this.doLogout}
                                >Or click here to logout.</button>
                            </div>
                        </div>
                    </div>
                </div>
            </Layout>
        );
    }
}


