import React, { Fragment } from "react";
import { NavLink, Route } from "react-router-dom";
import PagingArticleList from './../Home/PagingArticleList';

const renderArticleTabs = (tabs, parentUrl) => {
    return (
        <Fragment>
            <div className="articles-toggle">
                <ul className="nav nav-pills outline-active">
                    {tabs.map((tab, i) => (
                        <li className="nav-item" key={i}>
                            <NavLink
                                exact
                                className="nav-link"
                                to={`${parentUrl}${tab.url}`}
                            >
                                {tab.label}
                            </NavLink>
                        </li>
                    ))}
                </ul>
            </div>

            {tabs.map((tab, i) => (
                <Route
                    exact
                    path={`${parentUrl}${tab.url}`}
                    render={() => (
                        <PagingArticleList
                            api={(page) => tab.api(page)}
                        />
                    )}
                    key={i}
                />
            ))}
        </Fragment>
    );
};

export default renderArticleTabs;


