import React from 'react';
import Layout from './../Layout/Layout';
import ProfileHeader from './ProfileHeader';
import renderArticleTabs from './RenderArticleTabs';
import ArticleApi from '../../api/ArticleApi';

export default class ProfileRoute extends React.Component {
    render() {
        const { match, history } = this.props;
        let username = match.params.username;
        return (
            <Layout
                windowTitle={`@${username}`}
            >
                <div className="profile-page">

                    <div className="user-info">
                        <div className="container">
                            <div className="row">

                                <div className="col-md-10 col-md-offset-1">
                                    <ProfileHeader
                                        key={username}
                                        username={username}
                                        onInvalidProfile={() => history.push("/")}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="container">
                        <div className="row">

                            <div className="col-md-10 col-md-offset-1">
                                {renderArticleTabs(
                                    [
                                        {
                                            label: "My Articles",
                                            url: "",
                                            api: (page) => ArticleApi.getArticleListByAuthor(page, username),
                                        },
                                        {
                                            label: "Favorited Articles",
                                            url: "/favorites",
                                            api: (page) => ArticleApi.getFavoritedArticleList(page, username),
                                        }
                                    ],
                                    match.url
                                )}
                            </div>

                        </div>
                    </div>

                </div>
            </Layout>
        );
    }
}


